html,
body {
  background-color: var(--lightGray);
  font-family: 'Fira Sans Condensed', sans-serif;
  font-size: var(--defaultSize);
}

.default-wrapper {
  align-items: center;

  background-color: var(--white);
  display: flex;
  height: 100vh;
  justify-content: center;
  margin: 0 auto;

  max-width: 1280px;
  width: 100%;
}

.section-wrapper {
  box-shadow: 0 0 6px rgba(0, 0, 0, 0.25);
  box-sizing: border-box;
  margin: 0 auto;
  max-width: 320px;
  padding: 30px;
  width: 100%;
}

.profile-wrapper {
  display: grid;
  grid-template-columns: 50px 1fr;
  margin: 0 auto;
  max-width: 1280px;
  width: 100%;
}

.profile-inner {
  padding: 100px 0;
}
